.login-container {
    display: flex;
    flex-direction: column;
    max-width: 300px;
    margin: 40px auto;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    gap: 30px;
    background-color:rgba(102, 226, 218, 0.541);

    h1{
        text-align: center;
    }


    

  
    label {
      margin-bottom: 8px;
      font-weight: bold;
    }
  
    input {
      width: 100%;
      padding: 8px;
      margin-bottom: 16px;
      border: 1px solid #ccc;
      border-radius: 3px;
    }
  
    button {
      background-color: #007bff;
      font-family:Arial, Helvetica, sans-serif;
      letter-spacing: 1px;
      color: #fff;
      padding: 10px;
      border: none;
      border-radius: 3px;
      cursor: pointer;
      float: right;
     
  
      &:hover {
        background-color: #0056b3;
      }
    }
  }

