@import "../../../css-config/mixins.scss";
.video-layout{
  
    padding: 30px 10px;
    @include md{
        padding: 60px 10px;
    }


    .video-content{
        display: flex;
        // background-color: burlywood;
        // padding: 10px 10px;
        
        margin: auto;
        gap: 20px;
        flex-wrap: wrap;
        @include md{
            flex-wrap: nowrap;
            
            padding: 30px 20px;

        }

      

        .video-left{
            margin: 0 auto;
           
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: flex-start;
            // background-color: cadetblue;

            @include md{
                padding: 10px;
            }
            h1{
                font-size: 30px;
                font-weight: 900;
               
                line-height: 50px;
                color: rgba(0,0,0,0.8);
                text-align: left;
                word-spacing: 2px;
                letter-spacing: 2px;
    
                @include md{
                    font-size: 50px;
                    line-height: 80px;
                }
               
    
                span{
                    color: rgba(43, 61, 226, 0.934);
                }
            }
    
            h3{
            margin-top: 20px;
                font-size:18px;
               align-self: flex-start;
                word-spacing: 2px;
                letter-spacing: 2px;
                color: rgba(0,0,0,0.6);
                font-weight: 0;
                line-height: 35px;
                
                @include md{
                    // margin-top: 20px;
                    font-size:28px;
                }
            }

            .btn{
                padding: 14px 20px;
                align-self: center;
                margin-top: 30px;
                font-size: 19px;
                align-self: flex-start;

                @include md{
                    // align-self: self-start;
                }
            }
        }

        .video-right{
            // background-color: chartreuse;
            margin: 0 auto;
            display: flex;
            justify-content: center;
            align-items: center;
          
          

            @include md{
                padding: 10px;
                
            }

            .real-video{
                // background-image: url("../../../assets/laptop.png");
                // background-repeat: no-repeat;
                // background-size: 550px;
                // background-color: aqua;

                height: 250px;
            //    background-size: 100%;
                width: 100%;
                // position: relative;
               
                @include ml{
                    height: 350px;
                    width: 500px;
                }
                // margin: 0 auto;

               
               
          

                img{
                    height: 100%;
                    width: 100%;
                    -webkit-user-drag: none;
                    // position: absolute;
                   
                //    top: 40px;

                    
                  
                  
                }
            }
        }
        
    }
}