@import "../../../..//../../css-config/mixins.scss";


.join-now{
    height: fit-content;
    width: fit-content;
    padding: 8px;
    background-color: rgb(0, 128, 0);
    z-index: 12;
    position: fixed;
    text-align: center;
    width: 100%;
    color: white;
    font-size: 18px;
    letter-spacing: 2px;
    word-spacing: 2px;
    line-height: 30px;
    
  
   

    p{
        animation: line 5s infinite;
    }
    
}
.single-product-main-content {
    margin: 75px 0;
   

   



    .layout {
        max-width: calc(100% - 20px);
        margin: 0 auto;

        @include md {
            max-width: 1200px;
        }

        h1{
            text-align: center;
         
           
            background-color: rgba(0, 0, 255, 0.648);
            color: white;
            padding: 10px 0;
            line-height: 40px;
            text-transform: capitalize;
            font-size: 23px;
            opacity: 0;
    transform: translateY(20px);
    animation: fadeInUp 1s ease-in-out forwards;

            @include md{
                font-size: 30px;
                padding: 20px 0;
            }
        }

        h2{
            text-align: center;
            // background-color: rgba(0, 0, 255, 0.648);
            // color: white;
            padding: 10px 0;
            
            line-height: 40px;
            // text-transform: capitalize;
            font-size: 21px;
            opacity: 0;
    transform: translateY(20px);
    animation: fadeInUp 1s ease-in-out forwards;

            @include md{
                font-size: 24px;
                padding: 20px 0;
            }
        
        }

        p{
            // text-align: center;
            // background-color: rgba(0, 0, 255, 0.648);
            // color: white;
            padding: 10px 0;
            color: rgba(0,0,0,0.6);
            line-height: 30px;
            text-align: justify;
            // text-transform: capitalize;
            // font-size: 23px;
            opacity: 0;
    transform: translateY(20px);
    animation: fadeInUp 1s ease-in-out forwards;

            @include md{
                // font-size: 30px;
                padding: 20px 0;
            }   
        }

        ul{
            //   text-align: center;
            // background-color: rgba(0, 0, 255, 0.648);
            // color: white;
            padding: 10px 20px;
            color: rgba(0,0,0,0.6);
            line-height: 40px;
            background-color: rgba(0,0,0,0.06);
           
            list-style: none;
            // text-align: justify;
            // text-transform: capitalize;
            // font-size: 23px;
            opacity: 0;
    transform: translateY(20px);
    animation: fadeInUp 1s ease-in-out forwards;

            @include md{
                // font-size: 30px;
                padding: 20px 0;
                text-align: center;
               
            } 
        }


      

        .cta-button{
             
            // background-color: rgba(0, 0, 255, 0.648);
            // color: white;
            // padding: 30px 0;
            color: rgba(0,0,0,0.6);
            line-height: 80px;
            margin: 10px 0;
            // background-color: rgba(0,0,0,0.06);
           
           
            // text-align: justify;
            // text-transform: capitalize;
            font-size: 18px;
            opacity: 0;
    transform: translateY(20px);
    animation: fadeInUp 1s ease-in-out forwards;

           
        }

        .title {
            text-align: center;
            font-size: 24px;
            text-transform: capitalize;
            font-weight: 900;
          
    
            @include md {
                font-size: 34px;
                text-transform: capitalize;
    
            }

           
    
    
    
    
        }


        .single-product-page {
            margin-top: 20px;
            display: flex;
            flex-direction: column;

            align-items: center;


            @include mdd {
                flex-direction: row;
                margin-top: 80px;
            }

            .leftp {
                width: 100%;
                // background-color: rgba(0, 0, 0, 0.05);
                flex-shrink: 0;
                // transform-style: preserve-3d;
                // box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
                

                // box-shadow: 1px 1px 1px 1px;
                @include md {
                    width: 500px;
                    height: 300px;
                }

                img {
                    width: 100%;
                    display: block;
                    height: 100%;
                    // box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
                }


            }

            .right {
                display: flex;
                flex-direction: column;
                padding-top: 20px;

                @include md {
                    padding: 0 55px;

                    // margin-top: 90px;

                }


                .price {
                    font-size: 20px;
                    line-height: 32px;
                    margin-bottom: 20px;
                    text-transform: uppercase;
                    word-spacing: 8px;
                    font-weight: 700;

                    &:after {
                        content: "";
                        display: block;
                        margin-top: 5px;
                        width: 50px;
                        height: 3px;
                        background-color: #8e2de2;

                        @include md {
                            margin-top: 10px;
                        }
                    }

                    .buttonf{
                      
                      width: 140px;
                      font-size: 12px;

                      @include md{
                        font-weight: 600;
                        width: 160px;

                      }

                        svg{
                            font-size: 19px;
                        }
                    }


                


                }

                .desc {
                    text-indent: 50px;
                    text-align: justify;
                    font-size: 16px;
                    line-height: 20px;
                    margin-bottom: 20px;
                    color: #6b6b6b;
                    // padding:0 10px ;
                    line-height: 30px;

                    @include md {
                        font-size: 16px;
                        padding:0 10px ;
                        
                        
                    }
                }









            }
        }
    
    
 
    }
}

@keyframes example {

    0% {
        left: 0px;
        top: 0x;
    }

    100% {
        left: 50%px;
        top: 0px;
    }
}

@keyframes fadeInUp {
    from {
      opacity: 0;
      transform: translateY(20px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }


  @keyframes line{
    0%{transform: rotate(0);}
    50%{transform: rotate(1deg)}
    75%{transform: rotate(-1deg)}
  }