@import "../../../../css-config/mixins.scss";

.office{
    margin: 40px 0;
    text-align: center;
    line-height: 30px;

    @include md{
        margin: 50px 0;
    }


    .office-card{
        margin: 40px 0;
        display: flex;
        justify-content: center;
        gap: 50px;
        flex-flow: wrap;

        .office-card-first{
            height: fit-content;
            background-color: white;
            width: 300px;
            padding: 10px;
            box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
            border-radius: 20px;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            gap: 20px;

            p{
                word-spacing: 2px;
            }
        }
    }
}